import React, { useEffect, useState } from "react";
import { HStack, Text, Box, VStack } from "native-base";
// import { useLocation } from "react-router-dom";
import moment from "moment-timezone";
import {
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  AreaChart,
  Area,
  ResponsiveContainer,
} from "recharts";
import _ from "lodash";
import "react-datepicker/dist/react-datepicker.css";

import { percentDiff, vh, vw } from "../../../util";
import Colors from "../../../constants/Colors";
import { API_URL } from "../../../constants/Config";
import { useRef } from "react";

const h = window.screen.height * (35 / 100);

// function useQuery() {
//   const { search } = useLocation();
//   return React.useMemo(() => new URLSearchParams(search), [search]);
// }

const PlotChart = ({ type, range, id, role, fromdate, todate }) => {
  const [chartData, setChartData] = useState({});

  useEffect(() => {
    const getData = async () => {
      try {
        const result = await fetch(API_URL + "/analytics/data", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            // Authorization: "Bearer " + token,
          },
          body: JSON.stringify({ type, range, id, role, fromdate, todate }),
        });

        const response = await result.json();
        setChartData(response);
        return response;
      } catch (err) {
        console.log(err.message);
      }
    };
    (async () => {
      await getData();
    })();
  }, [range, fromdate, todate, id, role, type]);

  let label = "";
  if (type === "properties") {
    label = "Properties Added";
  } else if (type === "mailers") {
    label = "Mails Sent";
  } else if (type === "emails") {
    label = "Emails Sent";
  } else if (type === "investments") {
    label = "Total Investments";
  } else if (type === "milesdriven") {
    label = "Miles Driven";
  } else if (type === "timedriven") {
    label = "Time Driven";
  }

  const data = (chartData && chartData.data && chartData.data.data) || [];
  const curr = chartData && chartData.data && chartData.data.curr;
  const prev = chartData && chartData.data && chartData.data.prev;
  // const percent = (chartData && chartData.data && chartData.data.percent) || 0;

  const cdate = curr
    ? moment(curr.start).format("MMM DD, YYYY") +
      " - " +
      moment(curr.end).format("MMM DD, YYYY")
    : "-";
  const pdate = prev
    ? moment(prev.start).format("MMM DD, YYYY") +
      " - " +
      moment(prev.end).format("MMM DD, YYYY")
    : "-";

  let cTotal = (curr && curr.total) || 0;
  let pTotal = (prev && prev.total) || 0;

  let percent = 0;
  if (cTotal === 0 || pTotal === 0) {
    percent = 0;
  } else if (cTotal !== pTotal) {
    percent = percentDiff(pTotal, cTotal);
  }

  if (type === "investments") {
    cTotal = parseFloat(cTotal).toFixed(2);
    pTotal = parseFloat(pTotal).toFixed(2);
  }

  return (
    <VStack
      style={{
        // borderRadius: vw(10),
        // boxShadow: "0px 3px 10px #0000001A",
        width: "100%",
        height: h,
        // marginVertical: vh(20),
        padding: vh(20),
        backgroundColor: "white",
        margin:5
      }}
    >
      <HStack
        style={{ zIndex: -1, position: "relative" }}
        justifyContent={"space-between"}
      >
        <Text
          style={{
            fontFamily: "Poppins_medium",
            fontSize: vw(18),
            color: "#000000",
          }}
        >
          {label}
        </Text>
        <HStack justifyContent={"flex-end"}>
          <Text
            style={{
              fontSize: 21,
              marginTop: vh(-1),
              fontFamily: "Poppins_medium",
              color: percent >= 0 ? Colors.primary : "rgba(0,0,0,0.8)",
            }}
          >
            {percent > 0
              ? String.fromCharCode(8593)
              : percent < 0
              ? String.fromCharCode(8595)
              : ""}
          </Text>
          <Text
            style={{
              fontSize: 18,
              fontFamily: "Poppins_medium",
              color: percent >= 0 ? Colors.primary : "rgba(0,0,0,0.5)",
              marginLeft: vw(5),
            }}
          >
            {percent + "%"}
          </Text>
        </HStack>
      </HStack>
      <HStack justifyContent={"space-between"}>
        <Text
          style={{
            fontSize: vw(15),
            fontFamily: "Poppins_medium",
            color: Colors.primary,
          }}
        >
          {cTotal}
        </Text>
        <Text
          style={{
            fontSize: vw(15),
            fontFamily: "Poppins_medium",
            color: "rgba(0,0,0,0.5)",
          }}
        >
          {pTotal}
        </Text>
      </HStack>
      <HStack
        style={{ marginTop: vh(5), marginBottom: vh(10) }}
        justifyContent={"space-between"}
      >
        <Text
          style={{
            fontSize: vw(11),
            fontFamily: "Poppins_medium",
            color: "#919191",
          }}
        >
          {cdate}
        </Text>
        <Text
          style={{
            fontSize: vw(11),
            fontFamily: "Poppins_medium",
            color: "#919191",
          }}
        >
          {pdate}
        </Text>
      </HStack>
      <ResponsiveContainer width={"100%"} height={h - 120}>
        <AreaChart
          // width={730}
          // height={550}
          data={_.sortBy(
            data.map((d) => {
              if (type === "investments") {
                d.curr = d.curr.toFixed(2);
                d.prev = d.prev.toFixed(2);
              }
              return d;
            }),
            "date"
          )}
          margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
        >
          <defs>
            <linearGradient id='colorCurr' x1='0' y1='0' x2='0' y2='1'>
              <stop offset='5%' stopColor={Colors.primary} stopOpacity={0.8} />
              <stop offset='95%' stopColor={Colors.primary} stopOpacity={0} />
            </linearGradient>
            <linearGradient id='colorPrev' x1='0' y1='0' x2='0' y2='1'>
              <stop offset='5%' stopColor='rgba(0,0,0,0.4)' stopOpacity={0.8} />
              <stop offset='95%' stopColor='rgba(0,0,0,0.4)' stopOpacity={0} />
            </linearGradient>
          </defs>
          <XAxis style={{ fontSize: vw(12) }} dataKey='date' />
          <YAxis style={{ fontSize: vw(12) }} />
          <CartesianGrid strokeDasharray='3 3' />
          <Tooltip labelStyle={{ fontSize: vw(12) }} />
          <Area
            type='monotone'
            dataKey='prev'
            stroke='rgba(0,0,0,0.4)'
            fillOpacity={1}
            fill='url(#colorPrev)'
          />
          <Area
            type='monotone'
            dataKey='curr'
            stroke={Colors.primary}
            fillOpacity={1}
            fill='url(#colorCurr)'
          />
        </AreaChart>
      </ResponsiveContainer>
    </VStack>
  );
};

const AnalyticsChart = ({ profile, user }) => {
  const urlSearchParams = new URLSearchParams(window.location.search);
  const params = Object.fromEntries(urlSearchParams.entries());
  const pathname = window.location.pathname;
  const [period] = useState(params.fromdate || "7d");
  const [fromDate] = useState(params.fromdate || new Date());
  const [toDate] = useState(params.todate || new Date());  
  const type = pathname.replace("/analytics/", "");
  const role = params.role;
  const id = params.id;

  return (
    <PlotChart
      key={"chart-" + type}
      type={type}
      range={period}
      fromdate={moment(fromDate).format("YYYY-MM-DD")}
      todate={moment(toDate).format("YYYY-MM-DD")}
      id={id}
      role={role}
    />
  );
};

export default AnalyticsChart;
